import axios from 'axios';
import {AppCode} from './config.js'




export async function getNewsChannels(){
   var res= await axios.get("http://ali-news.showapi.com/channelList",{
        headers:{
            Authorization:`APPCODE ${AppCode}`
        }
    })
   
    return res.data.showapi_res_body.channelList;
}

export async function getnews(channelId,page=1,limit=10){
    var res= await axios.get("http://ali-news.showapi.com/newsList",{
        headers:{
            Authorization:`APPCODE ${AppCode}`
        },
        params:{
            channelId,
            page,
            maxResult:limit,
            needContent:1
        }
    });
    console.log(res.data);
    return res.data.showapi_res_body.pagebean;
// .contentlist
    //
}
getnews("5572a108b3cdc86cf39001ce");



