<template>
  <div class="news-header">
      <div class="logo">
        <router-link to="/"><img src="https://pic.rmb.bdstatic.com/b831597fe933bcf8595a6090a4f3da32.jpeg@wm_2,t_55m+5a625Y+3L+a1gea1queahOaAnee7tA==,fc_ffffff,ff_U2ltSGVp,sz_25,x_16,y_16" alt=""></router-link>
        
      </div>
      <nav>
          <ul>
            <li v-for="item in newArr" :key='item.channelId'><router-link  
            :to="{
              name:'ChallenNews',
              params:{
                id:item.channelId,
              }
              }">{{item.name}}</router-link></li>
            
          </ul>
      </nav>
      <div class="login">
          <router-link to="/login">登陆</router-link>
          <span>/</span>
          <router-link to="/reg">注册</router-link>
      </div>
  </div>
 
</template>

<script>
import { getNewsChannels } from '../services/newservices'

export default {
  data(){
    return{
      // navarr:['首页','国内焦点','国际焦点','军事焦点','财经焦点','互联网焦点']
      newArr:[]
    }
  },
   async created(){
      var res= await getNewsChannels(); 
      this.newArr=res.slice(0,5);
    }

}
</script>

<style scoped>

 .news-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;


}
 .news-header .logo{
  display: flex;
  width: 60px;
  height: 100px;
  align-items: center;
}

 .news-header .logo img{
  width: 100%;
  height: 60px;
  background: no-repeat center center/cover;
}
 .news-header nav{
  margin-left: -200px;
  width: 70%;
  height: 100px;
  font-size: 18px;
  font-weight: 100;
  


}
 .news-header nav ul{

  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  align-items: center;
}
 .news-header nav ul li{
  width: 20%;
  text-align: center;
}
 .news-header nav ul li a{
  height: 100px;
  display: inline-block;
  line-height: 100px;
  color: #fff;
}

 .news-header nav ul li:hover a{
  color: peru;
}
 .news-header .login{
  margin-top: 10px;
  width: 80px;
  height: 100px;
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
 .news-header .login a{
  color: #fff;
  font-weight: 300;
}
 .news-header .login a:hover{
  color: peru;
}
</style>


